import './style.css';
import about__pic from './materials/about_pic.jpg'

const About = () => {

    return ( 
        <section id="about"  className="about" >
            <div className="about__picture wow animate__animated animate__bounceInLeft">
                <img src={about__pic} alt="About"/>
            </div>
            <div className="about__text wow animate__animated animate__bounceInRight">
                <div className="about__texts">
                    <h2>Обо мне</h2>
                    <p>Свой путь в web-е я начал ещё в школе, мне стало интересно как устроены сайты. Погружаясь всё глубже и глубже я научился верстать простые одностраничные сайты-визитки, так и многостраничные, но я понял что этого мало. По этому я поставил для себя цель стать полноценным frontend-разработчиком. Что бы создавать более много задачные и функциональные сайты.</p>
                </div>
            </div>
        </section>
     );
}

export default About;