import './style.css';
import Slider from '@farbenmeer/react-spring-slider'

const Contact = () => {
    return (
        <section className="contacts">
            <div className="container">
                <div className="contact__socials">
                    <span className="wow animate__animated animate__fadeInLeft"><i className="fab fa-vk _fa-vk"/><a className="_fa-vk" href="https://vk.com/chopperxx">vk.com/chopperxx</a></span>
                    <span className="wow animate__animated animate__fadeInLeft" data-wow-delay="0.5s"><i className="fab fa-telegram-plane _fa-teleg" /><a className="_fa-teleg" href="https://teleg.run/chopperxx">@chopperxx</a></span>
                    <span className="wow animate__animated animate__fadeInLeft" data-wow-delay="1s"><i className="fab fa-github _fa-github" /><a className="_fa-github" href="https://github.com/chopperqt">github.com/chopperqt</a></span>
                    <span className="wow animate__animated animate__fadeInLeft" data-wow-delay="1.5s"><i className="fas fa-envelope _fa-enve" /><a className="_fa-enve" href="mailto:ilua292010@gmail.com">ilua292010@gmail.com</a></span>
                    <span className="wow animate__animated animate__fadeInLeft" data-wow-delay="2s"><i className="fas fa-mobile-alt _fa-mob" /><a className="_fa-mob" href="tel:+79656036522">+7(965)603-65-22</a></span>
                </div>
                <div className="fields">
                    <div className="short__field wow animate__animated animate__fadeInRight" >
                        <div className="text__field">
                            <p>Tell me your name*</p>
                        </div>
                        <div className="inputs__field">
                            <input  type="text" placeholder="First Name" autoComplete="off" />
                            <input autoComplete="off" type="text" placeholder="Last Name"/>
                        </div>
                    </div>
                    <div className="long__field wow animate__animated animate__fadeInRight" data-wow-delay="0.5s">
                        <div className="text__field">
                            <p>Enter your E-mail*</p>
                        </div>
                        <div className="inputs__field">
                            <input autoComplete="off" type="text" placeholder="E-mail"/>
                        </div>
                    </div>
                    <div className="long__field wow animate__animated animate__fadeInRight" data-wow-delay="1s">
                        <div className="text__field">
                            <p>Enter your phone number</p>
                        </div>
                        <div className="inputs__field">
                            <input autoComplete="off" type="text" placeholder="Phone number"/>
                        </div>
                    </div>
                    <div className="long__field wow animate__animated animate__fadeInRight" data-wow-delay="1.5s">
                        <div autoComplete="off" className="text__field">
                            <p>Enter your message*</p>
                        </div>
                        <div className="inputs__field">
                            <textarea autoComplete="off" type="text" placeholder="Message"/>
                        </div>
                    </div>
                    <div className="long__field wow animate__animated animate__fadeInRight" data-wow-delay="2s">
                        <button id="submitBtn">Submit</button>
                    </div>
                    
                </div>
            </div> 
        </section>
    );
}

export default Contact;