import './style.css';

import video from './materials/single-page-1.mp4';
import video2 from './materials/alanzd.mp4';


const Work = ({src,id,img,tegs,name,description,color}) => {
    
    return (
        <div className="work" >
            <div className="container__max" style={(id % 2 === 0) ? {background : "#f2f2f2"} : {background : "#fff"}}>
                <div className="container__mini">
                    <div className="work__text">
                        <h2>{name}</h2>
                    </div>
                    <div className="work__video">
                        <video src={img} autoPlay loop muted preload="auto"></video>
                    </div>
                    <div className="work__additinal__text">
                        <h3><span>tech:</span> html,css,react</h3>
                        <h3><span>year:</span> 2015 </h3>
                        <a href={src}>Visit site</a>
                    </div>
                </div>
            </div>
        </div>
    );
}
export default Work;